body {
  margin: 20px 8px 80px;
  background-image: url("../images/clean-textile.png");
}

div {
  background-color: #dddddd;
}

.color-compare {
  position: fixed;
  right: 20px;
  top: 20px;
  width: 350px;
  border: 1px solid;
  overflow: hidden;
  background-color: #fff;
}
.color-compare .version {
  width: 100%;
  background-color: #0076d6;
  color: #fff;
  padding: 2px 5px;
  border-bottom: 1px solid black;
}
.color-compare .version a {
  color: #fff;
}
.color-compare .box {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  background-color: #fff;
}
.color-compare .box > div {
  flex: 1 1 100%;
  padding: 10px;
  background-color: #fff;
}
.color-compare .box > div > div {
  height: 172px;
  width: 150px;
  border: 0 none;
  margin: 0 auto;
  background-color: #fff;
}
.color-compare .box > div > div .info {
  background-color: #fff;
  padding: 2px;
  width: 100%;
  text-align: center;
}
.color-compare .box > div input {
  margin: 5px auto 0;
  display: block;
  width: 100%;
}
.color-compare .box > div span {
  display: block;
  margin: 5px auto;
}

main {
  margin: 10px 0 0 95px;
}

.color-family {
  background-color: transparent;
  border: 2px dashed #adadad;
  border-radius: 15px;
  padding: 0 20px 20px;
  margin-bottom: 20px;
  overflow: hidden;
  max-width: 940px;
  cursor: grab;
}
.color-family .row {
  display: flex;
  flex-wrap: nowrap;
  background-color: transparent;
}
.color-family .row > div {
  height: 64px;
  flex: 0 0 64px;
  border: 1px solid #dddddd;
}
.color-family .row.colors > div, .color-family .row.vivid > div {
  cursor: pointer;
}
.color-family .row.colors > div.selected, .color-family .row.vivid > div.selected {
  border: 3px solid #0076d6;
}
.color-family .row.colors > div.selected[class*=blue], .color-family .row.colors > div.selected[class*=cyan], .color-family .row.colors > div.selected[class*=indigo], .color-family .row.vivid > div.selected[class*=blue], .color-family .row.vivid > div.selected[class*=cyan], .color-family .row.vivid > div.selected[class*=indigo] {
  border: 3px solid #e52207;
}
.color-family .row.number > div {
  height: auto;
  border: 1px solid transparent;
  background-color: #f1f3f6;
  padding: 2px;
}
.color-family .row.v > div:after {
  content: "v";
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 10px 10px;
  width: 100%;
  color: #fff;
  background-color: #454540;
}
footer a {
  color: #fff;
}

.about-toggle {
  position: fixed;
  top: 50px;
  left: 20px;
  height: 60px;
  width: 60px;
  background-color: #0076d6;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 35px;
  color: #0076d6;
  text-decoration: none;
  font-weight: bold;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.45);
}
.about-toggle .mark {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  height: 45px;
  width: 45px;
  border-radius: 100%;
}

.about {
  position: fixed;
  display: none;
  left: -200px;
  top: 0;
  height: 100vh;
  padding: 80px 50px;
  overflow-y: scroll;
  overflow-x: visible;
  transition: left 0.3s ease, box-shadow 0.3s ease;
  max-width: 800px;
  z-index: 999;
  background-color: #fff;
}
.about .x {
  position: absolute;
  top: 10px;
  right: 20px;
  color: #0076d6;
  text-decoration: none;
  font-weight: bold;
  font-size: 35px;
}
.about:target, .about[aria-expanded=true] {
  display: block;
  left: 0;
  outline: none;
  box-shadow: 3px 0 12px rgba(0, 0, 0, 0.25);
}
.about:target .menu-close, .about[aria-expanded=true] .about-close {
  z-index: 1001;
}
.about:target + .backdrop, .about[aria-expanded=true] + .backdrop {
  position: fixed;
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 998;
  background: #000;
  background: rgba(0, 0, 0, 0.85);
  cursor: default;
}